@import "../_list-config";

.list-content {
  // override default style
  .list-group-item {
    overflow: visible;
    white-space: normal;
  }
  .title {
    @include text-truncate();
    margin-bottom: .5em;
    font-size: pxToEm(18px);
    font-family: "Microsoft YaHei", "Microsoft JhengHei", STHeiti, MingLiu;
  }
  .text {
    color: $gray;
  }
}



