@import "list-config";

/*  */
/*  */
.list-group-item {
  @include text-truncate();

  // override bootstrapt
  border: none;

  margin-bottom: 0;
  &:first-child, &:last-child { border-radius: 0; }

  .time, .right-info {
    float: right;
    color: #999;
    padding-left: pxToEm(14px);
  }
  &.active .time {
    color: #ffffff;
  }
}


