@import "../_variables";
@import "../_mixins";

.first-title {
  padding-left: 12px;
  padding-right: 12px;
  line-height: 2.8em;
  margin-bottom: 0;
  margin-top: 6px;
  .icon {
    color: $activeColor;
    margin-right: 10px;
  }
}

#icon-nav {
  background-color: #FFFFFF;
  padding: 10px 0;
  .mui-table-view-cell {
    padding-top: 0;
    padding-bottom: 0;
    border: none;
  }
}

.hot-message {
  line-height: 24px;
  padding-top: .8em;
  padding-bottom: .8em;
  .icon {
    margin-right: 10px;
    margin-left: 5px;
    font-weight: bold;
    font-size: 22px;
    color: $brand-warning;
  }
  .hots {
    display: block;
    height: 20px;
    overflow: hidden;
    margin-left: 5px;
    font-size: 12px;
    color: $gray2;
  }

}

.items, .mui-slider-item {
  .media-newsitem:first-child {
    margin-top: 0;
  }
}
.media-newsitem {
  padding: 0 8px 8px;
  a {
    display: block;
    &:active { color: inherit; }
  }
  .layout-body {
    position: relative;
    padding-bottom: 1.2em;
  }

  .media-title {
    @include text-truncate();
    line-height: 2.8;
    padding-left: 8px;
    padding-right: 8px;
    margin-left: -8px;
    margin-right: -8px;
    border-bottom: 1px solid $grayBdLight;
    font-size: 16px;
    font-weight: normal;
    .label {
      color: $activeColor;
      margin-right: 1.5em;
      font-weight: normal;
      &:before {
        content: "";
        display: inline-block;
        position: relative;
        left: -6px;
        top: .1em;
        width: 3px;
        height: 1em;
        background-color: $activeColor;
      }
    }
  }
  &.share-item .media-title .label { color: $brand-success;
    &:before { background-color: $brand-success; }
  }

  .media-text {
    font-size: 15px;
    line-height: 1.3;
    color: $gray-light2;
    text-shadow: 0 0 .5px $gray-lighter;
  }
  .media-price {
    font-size: 17px;
    color: $brand-warning;
    margin-bottom: 0;
    text-shadow: 0 0 1px $brand-warning;
  }

  .thumbnail {
    width: 82px;
    height: 82px;
    line-height: middleImg(83px);
    margin-right: 15px;
  }
  .media-other {
    @include text-truncate();
    right: 0;
    line-height: 1.2;
    .right {
      color: $activeColor;
    }
    .left {
      color: $brand-success;
    }
  }

}


