.task-index {
  .mui-slider-indicator.mui-segmented-control, #sliderProgressBar {
    position: fixed;
    z-index: 1000;
  }
  .mui-segmented-control {
    top: 44px;
    background-color: #ffffff;
  }
  .mui-segmented-control .mui-control-item {
    line-height: 40px;
  }
  .head-filter, .head-filter .mui-tab-item { height: 40px; }

  #banner, .head-filter-box, #sliderProgressBar { top: 84px; }

  .mui-segmented-control.mui-segmented-control-inverted ~ .mui-slider-group .mui-slider-item {
    border-top: none;
  }
  .header-tabs-cont {
    padding-top: 84px;
    .mui-slider-item {
      min-height: 300px;
    }
  }
}


.task-detail {
  .task-detail-head, .task-detail-bottom {
    background-color: #ffffff;
  }
  .task-detail-bottom {
    margin-top: 12px;
  }
  .task-banner {
    img {
      width: 100%;
    }
  }
  .task-title-box, .task-status-box, .time-box, .flow-box {
    padding: .5em;
    line-height: 1.6;

  }
  .task-title-box {
    border-bottom: 1px solid $grayBorder;
    .myicon-integrity, .myicon-integrity {
      margin: 0 5px;
    }
    .title {
      font-size: 17px;
      font-weight: bold;
    }
    .intro {
      font-size: 15px;
      color: $gray-light2;
    }
  }
  .task-status-box {
    i { margin-right: 5px; }
    .price {
      font-size: 17px;
      color: $brand-warning;
    }
    .status {
      font-size: 15px;
    }
    .succeed, .intention {
      display: inline-block;
      width: 48%;
    }
    .succeed {
      color: $brand-success;
    }
    .intention {
      color: $brand-danger;
    }
  }
  .time-box {
    color: lighten($gray2, 10%);
  }
  .flow-box {
    position: relative;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    color: $gray-light2;
    background-color: #f9f9f9;
    .flow-item {
      position: relative;
      z-index: 2;
      padding: 0 .3em;
      background-color: #f9f9f9;
      @include text-truncate();
    }
    .item-ico {
      display: inline-block;
      width: 16px;
      height: 16px;
      margin-right: 5px;
      border-radius: 50%;
      background-color: #bfbfbf;
      border: 3px solid #e6e6e6;
      vertical-align: middle;
      margin-bottom: 3px;
    }
    &:before {
      content: "";
      height: 0;
      border-top: 2px dotted $gray-light2;
      position: absolute;
      left: .5em;
      right: .5em;
      top: 50%;
      z-index: 1;
    }
  }

  .mui-slider .mui-control-item {
    color: $gray2;
    &.mui-active {
      font-weight: bold;
    }
  }
  .mui-slider .mui-slider-item {
    min-height: 300px;
    padding: 12px;
  }
}



