@import "../widget/scss/variables";
@import "../widget/scss/mixins";

@import "../widget/scss/reset";
@import "../widget/scss/glob-comm";
@import "sprite";

@import "widget";

* {
  -webkit-user-select: initial;
}
img { vertical-align: middle; }

body {
  font-size: 14px;
  line-height: (20 / 14);
}

.mui-content {
  padding-top: 44px;
  padding-bottom: 50px;
}

.mui-btn-block {
  padding: 12px;
}
.mui-table-view {
  &:before, &:after { background-color: transparent; }
  .mui-table-view-cell .mui-navigate-right {
    padding-right: 35px;
  }
}
.mui-table-view-cell {
  &:after { right: 15px; }
}
.mui-tab-item {
  &.mui-active {
    .icon-angle-down {
      transform:rotate(180deg);
    }
  }
}

.mui-popup-title {
  font-family: "microsoft yahei", simsun;
  & + .mui-popup-text {
    margin-top: 10px;
  }
}

// ====================
// select
.select {
  position: relative;
  &::after {
    right: 15px;
    content: "\F107";
    font-family: jzfont;
    font-size: inherit;
    line-height: 1;
    position: absolute;
    top: 50%;
    display: inline-block;
    transform: translateY(-50%);
    text-decoration: none;
    color: #bbb;
    -webkit-font-smoothing: antialiased;
  }
  select { margin-bottom: 0; }
}
.title-bar {
  font-size: 14px;
  margin-bottom: 6px;
  .title {
    font-size: 1.2em;
    &:before { width: 4px; }
  }
  .title-nav li a { padding: 0 .5em; }
}

.block-wrap {
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 6px 12px;
  background-color: #FFFFFF;
  > .title, > .title-bar {
    margin-top: -6px;
    margin-left: -12px;
    margin-right: -12px;
    padding-left: 12px;
    padding-right: 12px;
  }
  .mui-table-view {
    margin: 0 -12px;
  }
}

// ====================
// 
#header {
  &.index-header {
    background-color: transparent;
    box-shadow: none;
    input[type=search] {
      height: 35px;
      background-color: #ffffff;
      margin: 10px 0;
    }
    .mui-btn {
      padding: 4px;
      color: #ffffff;
      font-size: 24px;
    }
    .mui-placeholder { margin-top: 10px; }
    .mui-icon { padding-top: 7px; }
  }
  .mui-input-row .mui-input-clear ~ .mui-icon-clear { top: 10px; }
}

// ====================
// 
#footer {
  height: 50px;
  .mui-tab-item { color: $gray2;
    .mui-icon { color: $gray-light2; }
    .mui-tab-label { margin-top: 5px; }
  }
}


// 
.news-detail {
  margin: 18px;
  .title, .time { margin: .5em 0; }
  .title {
    font-family: "microsoft yahei", simsun;
    font-size: 20px;
    line-height: 1.4;
    font-weight: bold;
  }
  .time {
    color: #999999;
  }
  .cont {
    line-height: 1.8;
    font-family: "microsoft yahei", simsun;
  }
  p {
    margin: 1em 0;
    font-size: 16px;
    color: #666666;
  }
}

// =================
// 
.head-filter-box {
  .mui-popover {
    top: 40px !important;
  }
}
.head-filter {
  position: relative;

  display: flex;
  // 
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
  align-items: center;
  align-content: space-around; // 

  .tab-item, .mui-tab-item {
    @include text-truncate();
    height: 50px;
    width: auto;
    padding: 0 8px;
    font-size: 16px;
    color: $gray-dark;


    // 
    display: flex;
    justify-content: center;
    align-items: center;
    // 
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: auto;
    [class^="icon-"], [class*=" icon-"] {
      margin-left: 5px;
    }
  }
}
.head-filter-cont {
  border-radius: 0;
  left: 0 !important;
  &, &.mui-bar-popover .mui-table-view, .mui-table-view {
    width: 100%;
    border-radius: 0;
    padding: 4px;
  }
  .mui-table-view-cell {
    &:hover, &.on {
      color: $activeColor;
    }
    &:after {
      left: 0;
      right: 0;
    }
  }
  .mui-table-view .mui-table-view-cell {
    &:first-child, &:last-child {
      &, & > a:not(.mui-btn) { border-radius: 0; }
    }
  }
  .mui-popover-arrow {
    display: none;
  }
}




